import { Component, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {

  title = 'Live Resume - Sebastin Ramos Gutirrez';

  constructor(
    private titleService: Title,
    private metaTagService: Meta
  ) { }

  ngOnInit(): void {

    this.titleService.setTitle(this.title);

    this.metaTagService.addTags([
      { name: 'keywords', content: 'Web Developer, Python Developer, Student, Ingeniera Civil en Informtica, ' +
          'Sebastian Ramos Gutierrez Live Resume, Sebastian Ramos Gutierrez Resume, Sebastian Ramos Gutierrez CV, ' +
          'Curriculum Sebastian Ramos Gutierrez, SRAMOS Resumo, sramos live resume' },
      { name: 'robots', content: 'index, follow' },
      { name: 'author', content: 'Guilherme Borges Bastos' },
      { name: 'viewport', content: 'width=device-width, initial-scale=1' },
      { name: 'date', content: '2020-08-31', scheme: 'YYYY-MM-DD' },
      { charset: 'UTF-8' }
    ]);

    this.metaTagService.updateTag(
      { name: 'description', content: 'Hello, ' +
          'I\'m a Web Developer  in training with experience in designing web and mobile projects. ' +
          'Find out more in my live-resume!' }
    );
  }
}
